@import 'shared/StyleValues.scss';
@import 'shared/Animations.scss';

.Newsfeed {
  position: absolute;
  background-size: cover;
  pointer-events: none;
  width: 100%;
  height: 100%;
  padding-top: $stepSize * 4;
  padding-bottom: $stepSize * 2;
  padding-left: 5%;
  padding-right: 5%;

  @include fade-in();

  .ArrowFrame {
    position: absolute;
    @include square(4);
    bottom: 5%;
    right: 12.5%;

    .Arrow {
      pointer-events: all;
      height: 100%;
      width: 100%;
      @include animate-move-y();

      &.Down {
        @include animate-rotate(360deg);
      }

      &.Up {
        @include animate-rotate(180deg);
      }
    }
  }

  .Container {
    pointer-events: all;
    height: 100%;
    overflow-x: hidden;

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    .FeedBox {
      margin: 0 auto;
      width: 1200px;
      height: 400px;
    }
  }
}
