@import 'shared/StyleValues.scss';
@import 'shared/Animations.scss';

.Modal {
  @include centerPosition(450px, 700px);
  display: block;
  border: 1px black solid;
  background-color: white;
  opacity: 1;

  overflow: hidden;

  &.Show {
    pointer-events: all;
  }

  &.Hide {
    pointer-events: none;
  }

  .Title {
    padding: 12.5% 5% 0 5%;
    text-align: center;
    font-size: 45px;
  }

  .Body {
    display: block;
    width: 100%;
    padding: 10px 5% 50px 5%;
    text-align: center;
    font-size: 20px;
  }

  .ButtonSet {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 80px;
    text-align: center;

    .Button {
      display: inline-block;
      margin-left: 75px;
      margin-right: 75px;
      font-size: 60px;
      line-height: calc(100% - 2px);
      @include underline(3px);
    }
  }

  p {
    margin-block-start: 0em;
    margin-block-end: 0em;
  }
}
